"use client";

import React, { useEffect } from "react";
import { BiCart, BiRupee, BiTrendingUp, BiCheckCircle } from "react-icons/bi";
import { BsPeople, BsHeart, BsAward } from "react-icons/bs";
import { FiUsers, FiDollarSign } from "react-icons/fi";
import { HiOutlineTicket } from "react-icons/hi";
import useAdminStore from "@/store/adminStore";

export default function AdminDashboard() {
  const {
    dashboardStats,
    isLoading,
    error,
    getDashboardStats
  } = useAdminStore();

  useEffect(() => {
    // Load stats on mount
    const loadStats = async () => {
      try {
        const response = await getDashboardStats();
        console.log('Dashboard Stats Loaded:', response?.data);
        console.log('Food Partners Count:', response?.data?.overview?.foodPartners);
        console.log('Health Partners Count:', response?.data?.overview?.healthPartners);
      } catch (error) {
        console.error('Failed to load dashboard stats:', error);
      }
    };
    
    loadStats();

    // Refresh stats when window gains focus (user returns to tab)
    const handleFocus = () => {
      getDashboardStats();
    };

    // Refresh stats periodically (every 30 seconds)
    const interval = setInterval(() => {
      getDashboardStats();
    }, 30000); // 30 seconds

    window.addEventListener('focus', handleFocus);

    return () => {
      window.removeEventListener('focus', handleFocus);
      clearInterval(interval);
    };
  }, [getDashboardStats]);

  const cards = [
    {
      title: "Total Users",
      count: dashboardStats?.overview?.totalUsers || 0,
      icon: <FiUsers size={32} />,
      bg: "bg-blue-100 text-blue-600",
      gradient: "from-blue-500 to-blue-600",
    },
    {
      title: "Active Campaigns",
      count: dashboardStats?.overview?.activeCampaigns || 0,
      icon: <BsHeart size={32} />,
      bg: "bg-green-100 text-green-600",
      gradient: "from-green-500 to-green-600",
    },
    {
      title: "Total Donations",
      count: `â‚¹${(dashboardStats?.financial?.totalRaised || 0).toLocaleString()}`,
      icon: <FiDollarSign size={32} />,
      bg: "bg-purple-100 text-purple-600",
      gradient: "from-purple-500 to-purple-600",
    },
    {
      title: "Volunteers",
      count: dashboardStats?.overview?.volunteers || 0,
      icon: <BsPeople size={32} />,
      bg: "bg-orange-100 text-orange-600",
      gradient: "from-orange-500 to-orange-600",
    },
    {
      title: "Food Partners",
      count: dashboardStats?.overview?.foodPartners || 0,
      icon: <BiCart size={32} />,
      bg: "bg-pink-100 text-pink-600",
      gradient: "from-pink-500 to-pink-600",
    },
    {
      title: "Health Partners",
      count: dashboardStats?.overview?.healthPartners || 0,
      icon: <BsAward size={32} />,
      bg: "bg-teal-100 text-teal-600",
      gradient: "from-teal-500 to-teal-600",
    },
    {
      title: "Coupons Generated",
      count: dashboardStats?.overview?.totalCoupons || 0,
      icon: <HiOutlineTicket size={32} />,
      bg: "bg-indigo-100 text-indigo-600",
      gradient: "from-indigo-500 to-indigo-600",
    },
    {
      title: "Completed Campaigns",
      count: dashboardStats?.overview?.completedCampaigns || 0,
      icon: <BiCheckCircle size={32} />,
      bg: "bg-green-100 text-green-600",
      gradient: "from-green-500 to-green-600",
    },
  ];

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-green-500"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen">
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded">
          <strong className="font-bold">Error!</strong>
          <span className="block sm:inline"> {error}</span>
        </div>
      </div>
    );
  }

  return (
    <>
      <div className="mb-8 flex items-center justify-between">
        <div>
          <h1 className="text-4xl font-bold text-gray-900 mb-2">Dashboard</h1>
          <nav className="text-gray-500 text-sm">
            <ol className="flex space-x-2 items-center">
              <li>
                <a href="/admin" className="hover:underline text-gray-600 hover:text-blue-600">
                  Home
                </a>
              </li>
              <li className="text-gray-400">/</li>
              <li className="text-gray-700">Dashboard</li>
            </ol>
          </nav>
        </div>
        <button
          onClick={() => {
            getDashboardStats();
          }}
          className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors flex items-center gap-2"
        >
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
          </svg>
          Refresh
        </button>
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6 mb-12">
        {cards.map((card, idx) => (
          <div
            key={idx}
            className="group bg-white rounded-2xl shadow-sm hover:shadow-xl transition-all duration-300 p-6 border border-gray-100 hover:border-transparent hover:scale-105 cursor-pointer overflow-hidden relative"
          >
            {/* Gradient background on hover */}
            <div className={`absolute inset-0 bg-gradient-to-br ${card.gradient} opacity-0 group-hover:opacity-5 transition-opacity duration-300`}></div>
            
            <div className="relative flex items-center justify-between">
              <div>
                <p className="text-gray-500 text-sm font-medium mb-2">
                  {card.title}
                </p>
                <h3 className="text-3xl font-bold text-gray-900 group-hover:text-gray-800 transition-colors">
                  {card.count}
                </h3>
              </div>
              <div
                className={`w-16 h-16 flex items-center justify-center rounded-xl ${card.bg} group-hover:scale-110 transition-transform duration-300 shadow-sm`}
              >
                {card.icon}
              </div>
            </div>
            
            {/* Animated bottom bar */}
            <div className={`absolute bottom-0 left-0 right-0 h-1 bg-gradient-to-r ${card.gradient} transform scale-x-0 group-hover:scale-x-100 transition-transform duration-300`}></div>
          </div>
        ))}
      </div>

    </>
  );
}


