"use client";

import React, { useState, useEffect } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import Image from "next/image";
import {
  BiGrid,
  BiCoin,
  BiCode,
  BiGroup,
  BiHeart,
  BiUserPlus,
  BiUserCircle,
  BiUser,
  BiLogOut,
  BiChevronDown,
  BiCalendarEvent,
  BiBell,
  BiMenu,
} from "react-icons/bi";

const sidebarData = {
  main: [
    { title: "Dashboard", icon: <BiGrid />, href: "/admin" },
  ],
  backendContent: [
    {
      title: "Donation Section",
      icon: <BiCoin />,
      children: [{ title: "Donation Data", href: "/admin/donations" }],
    },
    {
      title: "Coupon Section",
      icon: <BiCode />,
      children: [{ title: "Coupon Data", href: "/admin/coupons" }],
    },
    {
      title: "CrownFunding Section",
      icon: <BiGroup />,
      children: [
        { title: "Fundraised Data", href: "/admin/fundraised" },
        { title: "Fundraiser Request", href: "/admin/fundraiser-requests" },
        { title: "Fundraiser Live", href: "/admin/fundraiser-live" },
        { title: "Upload FundRaiser", href: "/admin/create-fundraiser" },
      ],
    },
    {
      title: "Volunteer Section",
      icon: <BiHeart />,
      children: [
        { title: "Volunteer Rqst Data", href: "/admin/volunteer-requests" },
        { title: "Our Volunteers", href: "/admin/volunteers" },
      ],
    },
    {
      title: "Partner Section",
      icon: <BiUserPlus />,
      children: [
        { title: "Partner Request", href: "/admin/partner-requests" },
        { title: "Add Dr. Partner", href: "/admin/create-doctor" },
        { title: "Add Food Partner", href: "/admin/create-restaurant" },
        { title: "Our Partners", href: "/admin/partners" },
      ],
    },
    {
      title: "Users",
      icon: <BiUserCircle />,
      children: [{ title: "Registered Users", href: "/admin/users" }],
    },
    {
      title: "Website Queries",
      icon: <BiBell />,
      children: [{ title: "Query Mail", href: "/admin/queries" }],
    },
    {
      title: "Events Section",
      icon: <BiCalendarEvent />,
      children: [
        { title: "Upload Events", href: "/admin/create-event" },
        { title: "Our Events", href: "/admin/events" },
      ],
    },
    { title: "Front Side", icon: <BiMenu />, href: "/" },
  ],
  profile: [
    { title: "Your Profile", icon: <BiUser />, href: "/admin/profile" },
    { title: "Log Out", icon: <BiLogOut />, href: "/logout" },
  ],
};

export default function AdminSidebar() {
  const pathname = usePathname();
  const [openMenus, setOpenMenus] = useState({});

  // Automatically open parent menu if current route matches any child
  useEffect(() => {
    // Check all menu items with children
    const allMenuItems = [
      ...sidebarData.backendContent,
      ...sidebarData.main,
      ...sidebarData.profile,
    ];

    setOpenMenus((prev) => {
      const updated = { ...prev };
      
      allMenuItems.forEach((item) => {
        if (item.children) {
          // Check if current pathname matches any child href
          const isActive = item.children.some((child) => {
            if (child.href === pathname) return true;
            // Also check if pathname starts with child href for nested routes
            if (pathname?.startsWith(child.href)) return true;
            return false;
          });
          
          // Only update if it's not already set or needs to be opened
          if (isActive && !updated[item.title]) {
            updated[item.title] = true;
          }
        }
      });

      return updated;
    });
  }, [pathname]);

  const toggleMenu = (title) => {
    setOpenMenus((prev) => ({ ...prev, [title]: !prev[title] }));
  };

  const renderMenuItem = (item) => {
    if (item.children) {
      return (
        <li key={item.title}>
          <button
            className="flex items-center justify-between w-full px-3 py-2.5 text-left rounded-md hover:bg-gray-50 transition-colors duration-200 group"
            onClick={() => toggleMenu(item.title)}
          >
            <span className="flex items-center gap-3">
              <span className="inline-block text-gray-500 group-hover:text-blue-600 text-lg">{item.icon}</span>
              <span className="text-sm font-medium text-gray-700 group-hover:text-gray-900">{item.title}</span>
            </span>
            <span
              className={`transition-transform duration-200 inline-block text-gray-400 ${
                openMenus[item.title] ? "rotate-180" : ""
              }`}
            >
              <BiChevronDown />
            </span>
          </button>
          {openMenus[item.title] && (
            <ul className="pl-9 mt-1 space-y-0.5">
              {item.children.map((child) => {
                const isActive = pathname === child.href || pathname?.startsWith(child.href);
                return (
                  <li key={child.title}>
                    <Link
                      href={child.href}
                      className={`block px-3 py-2 rounded-md text-sm transition-colors duration-200 ${
                        isActive
                          ? "bg-blue-50 text-blue-600 font-medium"
                          : "text-gray-600 hover:bg-gray-50 hover:text-blue-600"
                      }`}
                    >
                      {child.title}
                    </Link>
                  </li>
                );
              })}
            </ul>
          )}
        </li>
      );
    }

    return (
      <li key={item.title}>
        <Link
          href={item.href || "#"}
          className="flex items-center gap-3 px-3 py-2.5 rounded-md hover:bg-gray-50 transition-colors duration-200 group"
        >
          <span className="inline-block text-gray-500 group-hover:text-blue-600 text-lg">{item.icon}</span>
          <span className="text-sm font-medium text-gray-700 group-hover:text-gray-900">{item.title}</span>
        </Link>
      </li>
    );
  };

  return (
    <aside className="w-64 min-h-screen bg-white border-r border-gray-100 fixed left-0 top-0 overflow-y-auto shadow-sm">
      {/* Logo/Title */}
      <div className="px-4 py-5 border-b border-gray-100 flex items-center gap-3">
        <div className="w-10 h-10 bg-green-50 rounded-full flex items-center justify-center">
          <span className="text-green-600 text-2xl">ðŸŒ±</span>
        </div>
        <h2 className="text-lg font-bold text-gray-900">Care Foundation Trust</h2>
      </div>

      <div className="py-4">
        {/* Main Dashboard */}
        <ul className="space-y-0.5 mb-6 px-3">
          {sidebarData.main.map((item) => renderMenuItem(item))}
        </ul>

        {/* Backend Content Section */}
        <div className="mb-6">
          <h3 className="text-xs font-bold text-blue-400 uppercase tracking-wider mb-3 px-4">
            Backend Content
          </h3>
          <ul className="space-y-0.5 px-3">
            {sidebarData.backendContent.map((item) => renderMenuItem(item))}
          </ul>
        </div>

        {/* Profile Section */}
        <div>
          <h3 className="text-xs font-bold text-blue-400 uppercase tracking-wider mb-3 px-4">
            Profile Section
          </h3>
          <ul className="space-y-0.5 px-3">
            {sidebarData.profile.map((item) => renderMenuItem(item))}
          </ul>
        </div>
      </div>
    </aside>
  );
}


