import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import useAuthStore from '@/store/authStore';
import Cookies from 'js-cookie';

/**
 * Custom hook to protect admin routes
 * PROPERLY waits for user data before redirecting
 */
export function useAdminAuth() {
  const router = useRouter();
  const { user, isAuthenticated, isLoading, initializeAuth, getCurrentUser } = useAuthStore();
  const [isChecking, setIsChecking] = useState(true);

  useEffect(() => {
    let mounted = true;

    const checkAuth = async () => {
      try {
        // Check if token exists in cookie
        const token = Cookies.get('token');
        
        if (!token) {
          if (mounted) {
            router.replace('/login?redirect=/admin');
            setIsChecking(false);
          }
          return;
        }

        // Initialize auth
        initializeAuth();

        // Wait a bit for store to update
        await new Promise(resolve => setTimeout(resolve, 200));

        // Get updated user from store
        const currentUser = useAuthStore.getState().user;
        const currentAuth = useAuthStore.getState().isAuthenticated;
        const currentLoading = useAuthStore.getState().isLoading;

        // If still loading, wait more
        if (currentLoading) {
          await new Promise(resolve => setTimeout(resolve, 300));
        }

        // Get updated state again
        const finalUser = useAuthStore.getState().user;
        const finalAuth = useAuthStore.getState().isAuthenticated;

        if (!mounted) return;

        // If not authenticated, redirect to login
        if (!finalAuth) {
          router.replace('/login?redirect=/admin');
          setIsChecking(false);
          return;
        }

        // If authenticated but no user, try fetching
        if (finalAuth && !finalUser) {
          try {
            await getCurrentUser();
            await new Promise(resolve => setTimeout(resolve, 200));
            const updatedUser = useAuthStore.getState().user;
            
            if (!mounted) return;

            if (updatedUser) {
              if (updatedUser.role !== 'admin') {
                router.replace('/dashboard');
                setIsChecking(false);
                return;
              }
              // User is admin, allow rendering
              setIsChecking(false);
            } else {
              router.replace('/login?redirect=/admin');
              setIsChecking(false);
            }
          } catch (error) {
            console.error('Failed to get user:', error);
            if (mounted) {
              router.replace('/login?redirect=/admin');
              setIsChecking(false);
            }
          }
          return;
        }

        // If we have user, check role
        if (finalUser) {
          if (finalUser.role !== 'admin') {
            router.replace('/dashboard');
            setIsChecking(false);
            return;
          }
          // User is admin, allow rendering
          setIsChecking(false);
        } else {
          // No user but authenticated, might be loading - wait a bit more
          setTimeout(() => {
            if (!mounted) return;
            const lastUser = useAuthStore.getState().user;
            if (!lastUser) {
              router.replace('/login?redirect=/admin');
              setIsChecking(false);
            } else if (lastUser.role !== 'admin') {
              router.replace('/dashboard');
              setIsChecking(false);
            } else {
              setIsChecking(false);
            }
          }, 500);
        }
      } catch (error) {
        console.error('Auth check error:', error);
        if (mounted) {
          router.replace('/login?redirect=/admin');
          setIsChecking(false);
        }
      }
    };

    checkAuth();

    return () => {
      mounted = false;
    };
  }, [initializeAuth, getCurrentUser, router]);

  // Get latest state from store
  const currentUser = useAuthStore.getState().user;
  const currentAuth = useAuthStore.getState().isAuthenticated;

  const canRender = !isLoading && !isChecking && currentAuth && currentUser && currentUser.role === 'admin';

  return {
    user: currentUser,
    isAuthenticated: currentAuth,
    isLoading: isLoading || isChecking,
    isAdmin: currentUser?.role === 'admin',
    canRender
  };
}

